import React, { useEffect, useState, useRef } from "react";
import "../../../map.css";
import useOutsideClick from "../useOutsideClick";

const City = ({
  cities,
  selectedCities,
  setSelectedCities,
  selectedLanguage,
}) => {
  const [show, setSate] = useState(false);
  const ref = useRef();
  const [search, setSearch] = useState("");
  const [result, setResult] = useState([]);

  useOutsideClick(ref, () => {
    if (show) setSate(false);
  });

  const handleOptionChange = (i) => {
    if (selectedCities.some((c) => c === i)) {
      let arr = selectedCities.filter((x) => x !== i);
      setSelectedCities(arr);
    } else {
      setSelectedCities([...selectedCities, i]);
    }
  };

  useEffect(() => {
    const results = cities.filter((city) =>
      city.name.toLowerCase().includes(search.toLowerCase())
    );
    setResult(results);
  }, [search, cities]);

  return (
    <div className="community">
      <button
        className="btnSupport btn_city"
        id="btnSelect"
        onClick={() => setSate(!show)}
      >
        <label className="support">
          {selectedCities.length == 1
            ? selectedLanguage == "arm"
              ? `Ընտրված է ${selectedCities.length} համայնք`
              : `${selectedCities.length} communitiy is selected`
            : selectedCities.length > 0
            ? selectedLanguage == "arm"
              ? `Ընտրված է ${selectedCities.length} համայնք`
              : `${selectedCities.length} communities is selected`
            : selectedLanguage == "arm"
            ? `Համայնք`
            : `Community`}
        </label>
        <img
          src={require("../../../icon/arrow.svg").default}
          onClick={() => setSate(!show)}
        />
      </button>
      {show && (
        <div ref={ref} className="NestedSelect">
          <button className="city_search">
            <input
              className="input_search_city"
              type="text"
              value={search}
              placeholder={selectedLanguage == "arm" ? "Փնտրել" : "Search"}
              onChange={(e) => setSearch(e.target.value)}
            />
            <img
              onClick={() => setSearch("")}
              className="img_close_city"
              src={require("../../../icon/closing icon.svg").default}
            />
            <img
              className="img_search_city"
              src={require("../../../icon/search icon.svg").default}
            />
          </button>

          <div className="list city">
            {result.map((i) => (
              <li
                className="li1"
                key={i.id}
                onClick={() => handleOptionChange(i.id)}
                style={{
                  backgroundColor: selectedCities.some((item) => item === i.id)
                    ? "#A4C2D8"
                    : "#FAFAFA",
                }}
              >
                {i.name}
              </li>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default City;
